"use client";
import Link from "next/link";
import { useState, useEffect } from "react";

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => setScrolled(window.scrollY > 50);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const navPadding = scrolled ? "py-3" : "py-5";
  const logoScale = scrolled ? "scale-90" : "scale-100";
  const shadowClass = scrolled ? "shadow-lg" : "shadow-md";

  const menuItems = ["Home", "Destinations", "Packages", "About", "Contact"];

  return (
    <nav
      className={`fixed top-0 left-0 right-0 z-50 bg-white/80 backdrop-blur-lg transition-all duration-300 ${navPadding} ${shadowClass}`}
    >
      <div className="max-w-7xl mx-auto px-6 flex items-center justify-between">
        {/* Logo */}
        <Link
          href="/"
          className={`text-3xl font-bold text-sky-600 transform transition-all duration-300 ${logoScale}`}
        >
          Dream<span className="text-gray-800">Travels</span>
        </Link>

        {/* Desktop Menu */}
        <div className="hidden lg:flex items-center space-x-10 text-gray-800 font-medium">
          {menuItems.map((item) => (
            <Link
              key={item}
              href={`/${item.toLowerCase()}`}
              className="relative group transition duration-300 hover:text-sky-600"
            >
              <span className="transition-colors duration-300 group-hover:text-sky-600">
                {item}
              </span>
              <span className="absolute left-0 -bottom-1 w-0 h-0.5 bg-sky-600 transition-all duration-300 group-hover:w-full"></span>
            </Link>
          ))}

          {/* CTA */}
          <Link
            href="/packages"
            className="ml-6 px-6 py-2 bg-sky-600 text-white rounded-lg shadow hover:bg-sky-700 transition transform hover:-translate-y-0.5 hover:scale-105"
          >
            Book Now
          </Link>
        </div>

        {/* Mobile Button */}
        <button
          className="lg:hidden text-gray-800 text-3xl focus:outline-none"
          onClick={() => setIsOpen(true)}
        >
          ☰
        </button>
      </div>

      {/* Unified Glassy Side Menu */}
      <div
        className={`fixed top-0 right-0 h-screen bg-white/80 backdrop-blur-lg shadow-2xl border-l border-white/40 
        transition-all duration-500 ease-in-out transform
        ${isOpen ? "translate-x-0" : "translate-x-full"}
        rounded-tl-3xl
        `}
        style={{
          width: "clamp(230px, 35vw, 300px)",
        }}
      >
        {/* Close Button */}
        <button
          className="absolute top-6 right-6 text-3xl text-gray-700 hover:text-sky-600 transition"
          onClick={() => setIsOpen(false)}
        >
          ✕
        </button>

        {/* Menu Links */}
        <div className="flex flex-col items-center justify-center h-full space-y-6 text-gray-800 font-medium">
          {menuItems.map((item) => (
            <Link
              key={item}
              href={`/${item.toLowerCase()}`}
              onClick={() => setIsOpen(false)}
              className="text-lg hover:text-sky-600 transition-all duration-300"
            >
              {item}
            </Link>
          ))}

          <Link
            href="/packages"
            onClick={() => setIsOpen(false)}
            className="mt-4 px-6 py-2 bg-sky-600 text-white rounded-lg shadow hover:bg-sky-700 transition transform hover:-translate-y-0.5 hover:scale-105"
          >
            Book Now
          </Link>
        </div>
      </div>
    </nav>
  );
}
